import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiActionsheet from "@/components/actionsheet/actionsheet"
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiActionsheet
	},
	data() {
		return {
			record_list:[1],
			history: [],
			hot: [],
			key: "",
			showActionSheet: false,
			tips: "确认清空搜索历史吗？"
		}
	},
	onLoad() {
		this.get_record()
		this.get_hotSearch()
	},
	methods: {
		get_record(){
			let cache = uni.getStorageSync('record')
			this.record_list = cache?cache:this.record_list
			this.history = cache
		},
		get_hotSearch(){
			let cache = uni.getStorageSync('hotSearch')
			this.hot = cache
		},
		sub(){ 
			console.log('xx:',this.record_list)
			this.record_list.push(this.key)
			if(this.record_list.length > 9){
				this.record_list.splice(0,1)
			}
			uni.setStorageSync('record',this.record_list)
			uni.redirectTo({
				url:'../productList/productList?key='+this.key
			})
		},
		to_search(item){
			this.key = item
			this.sub()
		},
		back: function() {
			uni.navigateBack();
		},
		cleanKey: function() {
			this.key = ''
		},
		closeActionSheet: function() {
			this.showActionSheet = false
		},
		openActionSheet: function() {
			this.showActionSheet = true
		},
		itemClick: function(e) {
			let index = e.index;
			if (index == 0) {
				this.showActionSheet = false;
				this.history = []
			}
		}
	}
}
